/**
 * @name: printMethod
 * @param {String | HTMLElement} printContent
 * @param {String} title
 * @return: Promise
 */
function printMethod(printContent, title = document.title) {
  let iframe = document.createElement('IFRAME')
  iframe.style = 'position:absolute;width:0;height:0;top:-1000px;left:-5000px;visibility:hidden'
  let html = typeof printContent === 'string' ? printContent : printContent.innerHTML
  let headContent = document.querySelector('head').innerHTML
  let iframeHtml = `<head>${headContent}</head><body>${html}</body>`
  let body = document.body
  body.appendChild(iframe)
  let iframeWindow = iframe.contentWindow || iframe
  let iframeDoc = iframe.contentDocument ? iframe.contentDocument : iframeWindow.document
  iframeDoc.write('<!doctype html>', `<html>${iframeHtml}</html>`)
  iframeDoc.title = title
  return new Promise(resolve => {
    setTimeout(() => {
      resolve()
      iframeWindow.print()
      body.removeChild(iframe)
    }, 1000)
  })
}

export default printMethod