<script>
import printMethod from '@/lib/print-method'
import EchartBar from '@/components/echart/echartbarzhu'
import { formatDate } from '@/lib/utils'
// 人群字典
const CROWD = [
  // { name: '中医药', field: 'tcmNum', value: 0 },
  // { name: '传染病', field: 'contagionNum', value: 0 },
  // { name: '慢病管理', field: 'chronicDiseaseNum', value: 0 },
  // { name: '贫困人口', field: 'poorPeopleNum', value: 0 },
  // { name: '预防接种', field: 'vaccinateNum', value: 0 }
  { name: '高血压', field: 'hypertensionNum', value: 0 },
  { name: '2型糖尿病', field: 'diabetesNum', value: 0 },
  { name: '老年人', field: 'oldNum', value: 0 },
  { name: '孕产妇', field: 'pregnantNum', value: 0 },
  { name: '肺结核患者', field: 'phthisisNum', value: 0 },
  { name: '严重精神障碍患者', field: 'psychosisNum', value: 0 },
  { name: '0~6岁儿童', field: 'childrenNum', value: 0 }
]

export default {
  name: 'analysisreportDetails',
  components: {
    EchartBar
  },
  data() {
    return {
      logoUrl: '',
      orgName: '',
      oData: {},

      // 异常人员清单
      anomalyUsers: [],

      // 异常数据
      // anomalyDistributes: [],

      // 异常值历年对比
      contrastAnomalys: [],

      // 未检名单
      nocheckNames: [],

      // 重点人群图表数据
      crowdLabel: [],
      crowdValue: [],

      // 异常图表数据(疾病)
      anomalyLabel: [],
      anomalyValue: []
    }
  },
  created() {
    this.getReport(this.$route.query.id)
    this.getLoginOrg()
  },

  methods: {
    // 处理数据
    handleData(data) {
      this.oData = data
      this.sortCrowd(data)
      this.anomalyUsers = JSON.parse(data.anomalyUser)
      let anomalyDistributes = JSON.parse(data.anomalyDistribute)
      this.contrastAnomalys = JSON.parse(data.contrastAnomaly)
      this.nocheckNames = JSON.parse(data.nocheckName)
      anomalyDistributes = anomalyDistributes.sort((it, next) => next.TOTALCOUNT - it.TOTALCOUNT)
      this.anomalyLabel = anomalyDistributes.map(item => item.CHECKPROPOSAL)
      this.anomalyValue = anomalyDistributes.map(item => item.TOTALCOUNT)
    },

    // 排序重点人群
    sortCrowd(data) {
      let crowdLabel = []
      let crowdValue = []
      let crowd = CROWD.map(item => {
        item.value = data[item.field] || 0
        return item
      })
        .sort((it, next) => next.value - it.value)
        .forEach(t => {
          crowdLabel.push(t.name)
          crowdValue.push(t.value)
        })
      this.crowdValue = crowdValue
      this.crowdLabel = crowdLabel
    },

    // 比较上次和本次的异常值
    compare({ THISVALUE, LASTVALUE }) {
      if (!THISVALUE || !LASTVALUE) return 0
      let thisVal = Number(THISVALUE)
      let lastVal = Number(LASTVALUE)
      return thisVal === lastVal ? 0 : thisVal > lastVal ? 1 : -1
    },

    getReport(reportExamId) {
      this.$post('/analyzereport/getAnalyzeReportById', {
        reportExamId
      })
        .then(({ data }) => {
          this.handleData(data)
        })
        .catch(err => err && this.$message.error(err))
    },

    getLoginOrg() {
      this.$post('/sys/org/getLoginOrg')
        .then(({ data }) => {
          this.logoUrl = data.ologo
          this.orgName = data.oname
        })
        .catch(err => {
          err && this.$message.error(err)
        })
    },
    doPrint() {
      this.$confirm('确定打印视为打印一次！', {
        title: '温馨提示',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(() => {
          let loading = this.$loading({ text: '打印准备中...' })
          this.$post('/analyzereport/printAnalyzereport', {
            reportExamId: this.$route.query.id
          })
            .then(() => {
              console.log('统计打印成功')
              printMethod(this.$refs['print-html-box'], '体检分析报告')
              setTimeout(() => {
                loading.close()
              }, 800)
            })
            .catch(err => {
              loading.close()
              this.$message.error('打印记录失败，无法打印！')
            })
        })
        .catch(cancel => cancel)
    }
  },
  computed: {
    hasPrint() {
      let btnIds = this.$store.state.btnAuthorityIds
      return ~btnIds.indexOf('105006004')
    },
    year() {
      let start = formatDate(this.oData.registerStartDate) || ''
      let end = formatDate(this.oData.registerEndDate) || ''
      start = start.slice(0, 4)
      end = end.slice(0, 4)
      if (start === end) return start
      return `${start}至${end}`
    }
  },
  filters: {
    calcPercentage(num) {
      if (!num) return 0
      let n = (num * 100).toFixed(1)
      return `${n}%`
    }
  }
}
</script>
