      height: 32px;
      border-radius: 4px;
      text-align: center;
      line-height: 32px;
      cursor: pointer;
    }
    .onstepbtn {
      border: 1px solid #00d0a6;
      color: #00d0a6;
    }
    .onstepbtn:hover {
      background-color: #e3fef9;
      border: solid 1px #00d0a6;
    }
    .nextbtn {
      background-color: #00d0a6;
      color: #fff;
    }
    .nextbtn:hover {
      background-color: #2cdfbb;
    }
  }
}
</style>
